
import { FC } from 'react';

import styles from '../login/login.module.css';


export const CaseUserName:FC<{ message: string }> = ({ message }) => (
  <div data-test-id='hint' className={styles.div}>
  {
    message === '1' ? <span  style={{ color: 'rgb(244, 44, 79)' }}>      </span> :
    message === '2' ? <span>   <span  style={{ color: 'rgb(244, 44, 79)' }}> </span>  </span> :
    message === '3' ? <span>      <span style={{ color: 'rgb(244, 44, 79)' }}></span></span> : <span>      </span>
       }
</div>
);

